.wrapper {
  padding-top: 50px;
  background-color: #05619e;
  background-image: linear-gradient(#05619e, gray);
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  border-radius: 7px;
}